// ÊØè‰∏™Â±ûÊÄßÈÉΩÂàÜÈÖç‰∏Ä‰∏™Dep,watcher‰∏≠‰πüË¶ÅÂ≠òÊîæËøô‰∏™Dep,Â§öÂØπÂ§ö
let id = 0;
class Dep{
  constructor(){
    this.id = id++
    this.subs = [];//Â≠òÊîæ watcher
  }
  depend(){
    // depË¶ÅÂ≠òÊîæ watcher watcher‰πüË¶ÅÂ≠òÊîæ dep
    if(Dep.target){
      // Êäädep‰º†ÁªôwatcherÂ≠òÂÇ®
      Dep.target.addDep(this);
    }
  }
  addSub(watcher){
    this.subs.push(watcher);
  }
  notify(){
    this.subs.forEach((watcher)=> watcher.update())
  }
}

Dep.target = null

export function pushTarget(watcher){
  Dep.target = watcher
}

export function popTarget(watcher){
  Dep.target = null
}

export default Dep;