import { observer } from './observer/index'
import { isFunction } from './utils'

export function initState(vm){
// console.log("üöÄ ~ file: state.js ~ line 2 ~ initState ~ vm", vm)
  const options = vm.$options
  // NOTE: 1„ÄÅprops ÂàùÂßãÂåñ
  if(options.props){
    // initProps()

  }
  // NOTE: 2„ÄÅdata ÂàùÂßãÂåñ
  if(options.data){
    initData(vm)

  }
  // NOTE: 3„ÄÅcomputed ÂàùÂßãÂåñ
  if(options.computed){
    // initComputed()
  }
  // NOTE: 4„ÄÅwatch ÂàùÂßãÂåñ
  if(options.watch){
      // initWatch()
  }
}

function proxy(vm,source,key){
  Object.defineProperty(vm,key,{
    get() {
      return vm[source][key]
    },
    set(newValue){
      vm[source][key] = newValue
    }
  })
}

function initData(vm){
  let data = vm.$options.data
  // INFO: Vue2‰∏≠‰ºöÂ∞Üdata‰∏≠ÊâÄÊúâÁöÑÊï∞ÊçÆËøõË°åÂä´ÊåÅ Object.defineProperty
  // console.log(data)
  if(!isFunction(data)){
    console.error('data must be a function')
  }else{
    // TAG ÁªëÂÆöthis ÈÄöËøá_dataËøõË°åÂÖ≥ËÅî
    data = vm._data = data.call(vm)
  }
  // TAG Áî®Êà∑ vm.name ‰ª£ÁêÜ--> vm._data.name 
  for(let key in data){
    proxy(vm,'_data',key)
  }
  // NOTE: 1„ÄÅÊää data ÂèòÊàêÂìçÂ∫îÂºè
  observer(data)
} 