const oldArrayPrototype = Array.prototype
export const arrayMethods = Object.create(Array.prototype);
// Object.create ‰ΩøÁî®Áé∞ÊúâÁöÑÂØπË±°Êù•‰Ωú‰∏∫Êñ∞ÂàõÂª∫ÂØπË±°ÁöÑÂéüÂûã
let methods = [
  'push',
  'unshift',
  'splice',
  'pop',
  'reserve',
  'sort',
  'shift'
]
// Áî®Êà∑Ë∞ÉÁî®ÁöÑÊòØ‰ª•‰∏ä‰∏É‰∏™ÊñπÊ≥ï‰ºöÁî®Ëá™Â∑±ÈáçÂÜôÁöÑÔºåÂê¶ÂàôÂ∞±Áî®ÂéüÊù•ÁöÑÊï∞ÁªÑÂéüÂûã‰∏äÈù¢ÁöÑÊñπÊ≥ï
methods.forEach(method =>{
  arrayMethods[method] = function(...args){
    console.log('Êï∞ÁªÑÂèëÁîüÂèòÂåñ')
    // oldArrayPrototype[method].apply(this,args)
    oldArrayPrototype[method].call(this,...args)
    let inserted;
    let ob = this.__ob__;
    switch(method){
      case 'push':
      case 'unshift':
        inserted = args;
          
        break;
      case 'splice':
        inserted = args.slice(2)
        break;
      
      default:
        break;
    }
    // Â¶ÇÊûúÊúâÊñ∞Â¢ûÂä†ÁöÑÂÄºË¶ÅÁªßÁª≠ËøõË°åÂä´ÊåÅ
    if(inserted) ob.observerArray(inserted)
    
  }
})


