export function patch(oldVnode,vnode){
  // NOTE 1„ÄÅÁúüÂÆûdomËäÇÁÇπ
  
  if(oldVnode.nodeType == 1){
    // ÂÖàÊääÊñ∞ÁöÑËôöÊãüdomÂàõÂª∫‰∏∫ÁúüÁöÑdomÂÖÉÁ¥†ÔºåÊèíÂÖ•Âà∞ÂíåÂΩìÂâçÂêå‰∏ÄÂ±ÇÁ∫ßÔºåÂÜçÊääÂéüÊù•ÁöÑËá™Â∑±Âà†Êéâ
    const parentElm = oldVnode.parentNode
    console.log("üöÄ ", oldVnode)

    let elm = createElm(vnode)
    parentElm.appendChild(elm, oldVnode.nextSibling)

    parentElm.removeChild(oldVnode)
    
  }
}

function createElm(vnode){
  const {tag,attrs,children,text,vm} = vnode
  // ÊôÆÈÄöÂÖÉÁ¥†
  if(typeof tag === "string"){
    vnode.el = document.createElement(tag)
    children.forEach(child => {
      vnode.el.appendChild(createElm(child))
    });
  }else{
    vnode.el = document.createTextNode(text)
  }
  return vnode.el
}