import { isObject } from "../utils"
import { arrayMethods } from './array'
import Dep,{ pushTarget,popTarget}from './dep'

//INFO ËßÇÊµãËÄÖÁ±ª Á±ªÊúâÁ±ªÂûãÔºåÂØπË±°Ê≤°ÊúâÁ±ªÂûã
class Observer {
  constructor(data) {
    Object.defineProperty(data,'__ob__',{
      value:this,
      enumerable:false
    })
    // data.__ob__ = this;//ÊâÄÊúâË¢´Âä´ÊåÅÁöÑÂ±ûÊÄßÈÉΩÊúâ __ob__
    // NOTE 2„ÄÅÂØπdata‰∏≠ÁöÑÊï∞ÊçÆËøõË°åÈÅçÂéÜÂæ™ÁéØ Âä´ÊåÅ 
    if(Array.isArray(data)){ //Êï∞ÁªÑÂä´ÊåÅ 
      // ÂÜÖÈÉ®ÂØπÊï∞ÁªÑ‰∏çÈááÁî® Object.defineProperty
      // ÂØπÊï∞ÁªÑÂéüÊù•ÁöÑÊñπÊ≥ïËøõË°åÊîπÂÜô push shift pop unshift reserve sort splice/ 
      // __proto__ÊåáÂêëÊûÑÈÄ†ÂáΩÊï∞ÁöÑÂéüÂûãÔºàprototypeÔºâÂØπË±° 
      data._proto_ = arrayMethods
      // TAG Â¶ÇÊûúÊï∞ÁªÑÈáåÈù¢ÁöÑÊòØÂØπË±°ÔºåÂàôÈúÄË¶ÅÂä´ÊåÅÂØπË±°
      this.observerArray(data)

    }else{
      this.walk(data) //ÂØπË±°Âä´ÊåÅ
    }
    
  }
  // ÈÅçÂéÜÂØπË±°
  walk(data) {
    // NOTE 3„ÄÅÈÅçÂéÜÂØπË±°ÔºåÂìçÂ∫îÂºèÂä´ÊåÅ
    Object.keys(data).forEach((key)=> defineReactive(data,key,data[key]))
  }
  // ÈÅçÂéÜÊï∞ÁªÑ
  observerArray(data){
    data.forEach((item)=> observer(item))
  }

}

function defineReactive(data, key, value) {
  // NOTE 4„ÄÅvalueÊúâÂèØËÉΩÊòØÂØπË±°ÔºåÂÜçËøõË°åÈÄíÂΩíÂä´ÊåÅ
  observer(value)
  // ÊØè‰∏™Â±ûÊÄßÈÉΩÂØπÂ∫î‰∏Ä‰∏™ dep
  let dep = new Dep()
  Object.defineProperty(data, key,{
    get(){
      // ÂèñÂÄºÁöÑÊó∂ÂÄôÂ∞ÜwtacherÂíådepÂÖ≥ËÅîËµ∑Êù•
      // Dep.target
      if(Dep.target){
        dep.depend()
      }
      return value;
    },
    set(newValue){
      // NOTE 5„ÄÅÂ¶ÇÊûúÁî®Êà∑ËµãÂÄº‰∏Ä‰∏™Êñ∞ÁöÑÂØπË±°ÈúÄË¶ÅÂ∞ÜËøô‰∏™ÂØπË±°‰πüËøõË°åÂä´ÊåÅ
      // TAGÔºöÂè™ÊúâÊñ∞Â¢ûÂä†ÁöÑÂ±ûÊÄßÊòØÂØπË±°Êâç‰ºöËøõË°åÁõëÂê¨Âä´ÊåÅ
      if(newValue !== value){
        observer(newValue)
        value = newValue
        // ÂëäËØâÂΩìÂâçÁöÑÂ±ûÊÄßÂ≠òÊîæÁöÑwtacherÊâßË°åget()
        dep.notify()
      }

    }
  })
}

export function observer(data){
  // NOTE: 1„ÄÅÂ¶ÇÊûúÊòØÂØπË±°ÊâçËøõË°åËßÇÊµã
  if(!isObject(data)){
    return 
  }
  if(data.__ob__){
    return
  }
  new Observer(data)
}