import { initState } from "./state"
import { compileToFunction } from './compiler/index'
import { mountComponent } from "./lifecycle"

export function initMixin(Vue){
  Vue.prototype._init = function(options){
    //TAGÔºöthisÂíåvm‰øùÂ≠òÁöÑÊòØÂêå‰∏Ä‰∏™ÂØπË±°ÁöÑÂú∞ÂùÄÔºåÊâÄ‰ª•vm‰∏äÈù¢Â¢ûÂä†ÂÜÖÂÆπ‰∫ÜÔºåÂØπÂ∫îÁöÑVue
    //TAGÔºöÂÆû‰æãÂØπË±°‰πü‰ºöÂ¢ûÂä†
    const vm = this
    vm.$options = options

    //NOTEÔºö1„ÄÅÂØπÊï∞ÊçÆËøõË°åÂàùÂßãÂåñ
    initState(vm)
    //NOTE: 2„ÄÅÁºñËØëÊåÇËΩΩ

    if(vm.$options.el){
      // Â∞ÜÊï∞ÊçÆÊåÇËΩΩÂà∞Ëøô‰∏™Ê®°Êùø‰∏äÈù¢
      vm.$mount(vm.$options.el)
    }
  }

  Vue.prototype.$mount = function(el){
    const vm = this;
    const options = vm.$options
    el = typeof el === 'string' ? document.querySelector(el) : el
    vm.$el = el
    
    // Â¶ÇÊûúÁî®Êà∑Ê≤°Êúâ‰º†ÂÖ•renderÂáΩÊï∞
    if(!options.render){
      let template =  options.template 
      // Ê≤°Êúâ‰º† template‰∏î el Â≠òÂú®
      if(!template && el){
        // ÂèñÊúÄÂ§ñÂ±ÇÁöÑÈÇ£‰∏™ dom Â≠óÁ¨¶‰∏≤ outerHTML
        template = el.outerHTML
        // ÊääÊ®°ÊùøÂèòÊàê render ÂáΩÊï∞
        const render = compileToFunction(template)
        // console.log(render)
        options.render = render
      }
    }
    // Ë∞ÉÁî® renderÊñπÊ≥ïÊ∏≤Êüì‰∏∫ÁúüÊ≠£ÁöÑdomÊõøÊç¢È°µÈù¢ÁöÑÂÜÖÂÆπ
    //NOTE ÁªÑ‰ª∂ÁöÑÁ¨¨‰∏ÄÊ¨°ÊåÇËΩΩ
    mountComponent(vm,el) 
  }
}
