// Regular Expressions for parsing tags and attributes
// NOTE Ê†áÁ≠æÂêç
const ncname = `[a-zA-Z_][\\-\\.0-9_a-zA-Z]*`
// NOTE Ê†áÁ≠æÂêçÊçïËé∑ ÈÄöËøáÊ≠£ÂàôÁöÑÂàÜÁªÑÊü•Êâæ
const qnameCapture = `((?:${ncname}\\:)?${ncname})`
// NOTE ÂåπÈÖçÂºÄÂßãÊ†áÁ≠æ
const startTagOpen = new RegExp(`^<${qnameCapture}`)
// NOTE ÂåπÈÖçÊï¥‰∏™Ê†áÁ≠æÁöÑÂÖ≥Èó≠
const startTagClose = /^\s*(\/?)>/
// NOTE ÂåπÈÖçÁªìÊùüÊ†áÁ≠æ
const endTag = new RegExp(`^<\\/${qnameCapture}[^>]*>`)
// NOTE ÂåπÈÖçÂ±ûÊÄß
const attribute = /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/
const dynamicArgAttribute = /^\s*((?:v-[\w-]+:|@|:|#)\[[^=]+\][^\s"'<>\/=]*)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/
const doctype = /^<!DOCTYPE [^>]+>/i
// #7298: escape - to avoid being pased as HTML comment when inlined in page
const comment = /^<!\--/
const conditionalComment = /^<!\[/
// NOTE ÂåπÈÖç {{ }}
const defaultTagRe = /\{\{((?:.|\r?\n)+?)\}\}/g

function createAstElement(tagName, attrs){
  return {
    tag:tagName,
    type:1,
    children:[],
    parent:null,
    attrs
  }
}
let root = null
let stack = []
function start(tagName,attributes){
  let parent = stack[stack.length - 1]
  let element = createAstElement(tagName, attributes)
  if(!root){
    root = element
  }
  element.parent = parent
  if(parent){
    parent.children.push(element)
  }
  stack.push(element)
}
function end(tagName){
  let last = stack.pop()
  if(last.tag !==  tagName){
    throw new Error("Ê†áÁ≠æÊúâËØØ")
  }
}
function chars(text){
  text = text.replace(/\s/g,"")
  let parent = stack[stack.length - 1]
  if(text){
    parent.children.push({
      type:3,
      text
    })
  }
}
export function parserHTML(html){
  function advance(len){
    html = html.substring(len)
  }
  function parseStartTag(){
    const start = html.match(startTagOpen)
    if(start){
      const match = {
        tagName: start[1],
        attrs:[]
      }
      advance(start[0].length)
      let end
      let attr
      while(!(end = html.match(startTagClose)) && (attr = html.match(attribute))){
        match.attrs.push({name:attr[1],value:attr[3] || attr[4] || attr[5]})
        advance(attr[0].length)
      }
      if(end){
        advance(end[0].length)
      }
      return match;
    }
    return false;
  }
  // Ëß£Êûê template Â≠óÁ¨¶‰∏≤
  while(html){ //ÁúãËß£ÊûêÁöÑÂÜÖÂÆπÊòØÂê¶Â≠òÂú®
    let textEnd = html.indexOf('<')
    if(textEnd == 0){
      const startTagMatch = parseStartTag() //Ëß£ÊûêÂºÄÂßãÊ†áÁ≠æ
      if(startTagMatch){
        start(startTagMatch.tagName,startTagMatch.attrs,)
        continue
      } 
      const endTagMatch = html.match(endTag) //Ëß£ÊûêÁªìÊùüÊ†áÁ≠æ
      if(endTagMatch){
        end(endTagMatch[1])
        advance(endTagMatch[0].length)
        continue
      }
    }
    let text; // 1234</div>
    if(textEnd > 0){
      text = html.substring(0,textEnd)
    }
    if(text){
      chars(text)
      advance(text.length)
    }
  }
  return root
}
